import React, { useState } from 'react';
import Modal from 'react-modal';
import styles from './SpaceApplication.module.css'; // Asigur-te c numele este corect

// Seteaz elementul root pentru modal
Modal.setAppElement('#root');

const SpaceApplication = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    organization: '',
    organizationName: '',
    phone: '',
    date: '',
    email: '',
    eventName: '',
    eventDescription: '',
    startTime: '',
    endTime: '',
    participants: '',
    agreement: false
  });

  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const validate = () => {
    const newErrors = {};
    if (!formData.firstName) newErrors.firstName = 'Numele este obligatoriu';
    if (!formData.lastName) newErrors.lastName = 'Prenumele este obligatoriu';
    if (!formData.organization) newErrors.organization = 'Organizaia este obligatorie';
    if (!formData.organizationName) newErrors.organizationName = 'Denumirea organizaiei este obligatorie';
    if (!formData.phone.match(/^\d{10}$/)) newErrors.phone = 'Numrul de telefon nu este valid';
    if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) newErrors.email = 'Email-ul nu este valid';
    if (!formData.date) newErrors.date = 'Data este obligatorie';
    if (!formData.eventName) newErrors.eventName = 'Denumirea evenimentului este obligatorie';
    if (!formData.eventDescription) newErrors.eventDescription = 'Descrierea evenimentului este obligatorie';
    if (!formData.startTime) newErrors.startTime = 'Ora de nceput este obligatorie';
    if (!formData.endTime) newErrors.endTime = 'Ora de sfrit este obligatorie';
    if (!formData.participants || formData.participants <= 0) newErrors.participants = 'Numrul de participani trebuie s fie pozitiv';
    if (!formData.agreement) newErrors.agreement = 'Trebuie s accepi regulile centrului';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (event) => {
    const { id, value, type, checked } = event.target;
    setFormData({
      ...formData,
      [id]: type === 'checkbox' ? checked : value
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!validate()) {
      return;
    }

    setLoading(true);

    try {
      const response = await fetch('/api/space-application', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });

      if (response.ok) {
        alert('Aplicaia a fost trimis cu succes!');
        handleReset();
      } else {
        alert('A aprut o eroare la trimiterea aplicaiei.');
      }
    } catch (error) {
      console.error('Eroare la trimiterea aplicaiei:', error);
      alert('A aprut o eroare la trimiterea aplicaiei. Te rugm s ncerci din nou mai trziu.');
    } finally {
      setLoading(false);
    }
  };

  const handleReset = () => {
    setFormData({
      firstName: '',
      lastName: '',
      organization: '',
      organizationName: '',
      phone: '',
      date: '',
      email: '',
      eventName: '',
      eventDescription: '',
      startTime: '',
      endTime: '',
      participants: '',
      agreement: false
    });
    setErrors({});
  };

  const openModal = () => {
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  return (
    <section className={styles.spaceApplication}>
      <div className={styles.container}>
        <h2>Spaiu gratuit pentru orice iniiativ de tineret</h2>
        <ul className={styles.rulesList}>
          {/* Reguli */}
        </ul>
        <h3>Aplic pentru a beneficia de spaiu</h3>
        <form className={styles.form} onSubmit={handleSubmit} onReset={handleReset}>
          {/* Formular */}
          <div className={styles.formGroup}>
            <label htmlFor="firstName">Nume</label>
            <input
              type="text"
              id="firstName"
              value={formData.firstName}
              onChange={handleInputChange}
              required
            />
            {errors.firstName && <span className={styles.error}>{errors.firstName}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="lastName">Prenume</label>
            <input
              type="text"
              id="lastName"
              value={formData.lastName}
              onChange={handleInputChange}
              required
            />
            {errors.lastName && <span className={styles.error}>{errors.lastName}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="organization">Organizaia</label>
            <input
              type="text"
              id="organization"
              value={formData.organization}
              onChange={handleInputChange}
              required
            />
            {errors.organization && <span className={styles.error}>{errors.organization}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="organizationName">Denumirea organizaiei</label>
            <input
              type="text"
              id="organizationName"
              value={formData.organizationName}
              onChange={handleInputChange}
              required
            />
            {errors.organizationName && <span className={styles.error}>{errors.organizationName}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="phone">Numrul de telefon</label>
            <input
              type="tel"
              id="phone"
              value={formData.phone}
              onChange={handleInputChange}
              required
            />
            {errors.phone && <span className={styles.error}>{errors.phone}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="date">Data</label>
            <input
              type="date"
              id="date"
              value={formData.date}
              onChange={handleInputChange}
              required
            />
            {errors.date && <span className={styles.error}>{errors.date}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              value={formData.email}
              onChange={handleInputChange}
              required
            />
            {errors.email && <span className={styles.error}>{errors.email}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="eventName">Denumirea evenimentului</label>
            <input
              type="text"
              id="eventName"
              value={formData.eventName}
              onChange={handleInputChange}
              required
            />
            {errors.eventName && <span className={styles.error}>{errors.eventName}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="eventDescription">Descrierea evenimentului</label>
            <textarea
              id="eventDescription"
              value={formData.eventDescription}
              onChange={handleInputChange}
              required
            ></textarea>
            {errors.eventDescription && <span className={styles.error}>{errors.eventDescription}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="startTime">Ora de nceput</label>
            <input
              type="time"
              id="startTime"
              value={formData.startTime}
              onChange={handleInputChange}
              required
            />
            {errors.startTime && <span className={styles.error}>{errors.startTime}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="endTime">Ora de sfrit</label>
            <input
              type="time"
              id="endTime"
              value={formData.endTime}
              onChange={handleInputChange}
              required
            />
            {errors.endTime && <span className={styles.error}>{errors.endTime}</span>}
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="participants">Numrul de participani</label>
            <input
              type="number"
              id="participants"
              value={formData.participants}
              onChange={handleInputChange}
              required
              min="1"
            />
            {errors.participants && <span className={styles.error}>{errors.participants}</span>}
          </div>
          <div className={styles.formGroup}>
            {/* <label>
              <input
                type="checkbox"
                id="agreement"
                checked={formData.agreement}
                onChange={handleInputChange}
              />
              Confirm c sunt de acord cu regulile centrului cu privire la utilizarea spaiului i cu declaraia pe propria rspundere de folosire a spaiului.
            </label> */}
            {errors.agreement && <span className={styles.error}>{errors.agreement}</span>}
            <span
              className={styles.agreementLink}
              onClick={openModal}
              style={{ cursor: 'pointer', color: '#007bff' }}
            >
              Confirm c sunt de acord cu regulile centrului cu privire la utilizarea spaiului i cu declaraia pe propria rspundere de folosire a spaiului.
            </span>
          </div>
          <div className={styles.buttonGroup}>
            <button type="submit" className={styles.submitButton} disabled={loading}>
              {loading ? 'ncrcare...' : 'Trimite'}
            </button>
            <button type="reset" className={styles.resetButton}>Reseteaz</button>
          </div>
        </form>
      </div>

      {/* Modalul */}
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Reguli de Utilizare"
        className={styles.modal}
        overlayClassName={styles.overlay}
      >
        <h2>Reguli de Utilizare a Spaiului</h2>
        <ul>
          <li>Aplicarea pentru spaiu se face cu minim 3 zile nainte de activitate.</li>
          <li>Consumabilele(materialele) necesare pentru activitate nu pot fi achiziionate/mprumutate de la STUDEM.</li>
          <li>Spaiul trebuie rentors exact n starea n care a fost primit (dup orice activitate se face curenie, mobilierul este aranjat ca la primirea spaiului).</li>
          <li>Toate bunurile din cadrul centrului nu trebuie sa fie deteriorate.</li>
          <li>Centrul nu duce rspundere de lucrurile personale ale beneficiarilor.</li>
          <li>Nu aruncai fotoliile sac in aer i nu sariti pe ele!</li>
          <li>Dac nainte sau dup activitatea dvs. este un alt eveniment atunci este nevoie de o pauz de minim 1 ora.</li>
          <li>In cadrul activitatilor dvs. nu trebuie sa se promoveze violenta si discriminarea.</li>
          <li>n cazul n care mobilierul sau alt bun al centrului va fi deteriorat dup evenimentul dvs., acesta va fi reparat/cumprat de ctre dvs.</li>
          <li>La nceputul activitii se semneaz acordul pe proprie rspundere, n care este stipulat c dvs suntei de acord cu toate regulile stipulate de ctre Centrul de Tineret- STUDEM. n cazul n care acordul nu este semnat de ctre organizator, spaiul nu este oferit.</li>
        </ul>
        <button onClick={closeModal} className={styles.closeButton}>nchide</button>
      </Modal>
    </section>
  );
};

export default SpaceApplication;
